.modal--brief .modal__content {
  max-width: 830px; }

.modal--brief .modal__title {
  margin-bottom: 12px;
  font-size: 36px;
  line-height: 42px; }

.modal--brief .modal__form {
  margin-top: 40px; }

@media (max-width: 767px) {
  .modal--call-back .modal__content,
  .modal__content {
    max-width: 450px;
    padding: 40px 50px; } }

@media (max-width: 569px) {
  .modal--call-back .modal__content,
  .modal__content {
    padding-left: 20px;
    padding-right: 20px; } }

.overflow-modal {
  overflow: hidden;
  padding-right: 17px; }

@media (max-width: 1169px) {
  .overflow-modal {
    padding-right: 0; } }
